.controls {
  margin: 0;
  padding: 0 25px 0;
  display: none;
}

.controls__scale {
  position: relative;
  height: 1px;
  background-color: #f0eae4;
  margin: 0 0 48px 0;

  label {
    @include font(500, 14px, 18px);
    position: absolute;
    top: 14px;
  }

  label:nth-child(1n) {
    width: 40px;
    left: 18%;
  }

  label:last-child {
    width: 40px;
    left: 70%;
  }
}

.controls__bar {
  position: absolute;
  top: 0;
  left: 30%;
  width: 45%;
  height: 1px;
  background-color: $brown;
}

.controls__toggle {
  position: absolute;
  top: -7.5px;
  width: 15px;
  height: 15px;
  padding: 0;
  background-color: $brown;
  border-radius: 50%;
  cursor: pointer;

  &:hover {
    opacity: 0.7;
  }

  &:focus {
    box-shadow: 0 0 16px $brown;
  }

  &--left {
    left: 22%;
  }

  &--right {
    left: 75%;
  }
}
